import React, { useState, useEffect } from 'react';
import { Redirect, Route } from 'react-router';
import { auth } from './firebase/firebase.utils';
import './App.css';

import ChatPage from './pages/chatpage.component';
import HomePage from './pages/homepage.component';
import SignPage from './pages/signpage.component';
import LoginPage from './pages/loginpage.component';

function App() {
	const [isLoggedIn, setIsLoggedIn] = useState(false);
	const [userInfo, setUserInfo] = useState({ id: '', name: '' });

	useEffect(() => {
		const unsubFromAuth = auth.onAuthStateChanged(async userAuth => {
			console.log('Auth state changed: ', userAuth);
			if (userAuth) {
				console.log('A new user logged in');
				setUserInfo({
					id: userAuth.uid,
					name: userAuth.displayName,
				});
				setIsLoggedIn(true);
			} else {
				setIsLoggedIn(false);
				setUserInfo({ id: '', name: '' });
			}
		});

		return () => unsubFromAuth();
	}, []);

	return (
		<div className='App'>
			<Route
				exact
				path='/TheChat_App'
				render={props => <HomePage {...props} isLoggedIn={isLoggedIn} />}
			/>
			<Route path='/TheChat_App/login' component={LoginPage} />
			<Route path='/TheChat_App/signin' component={SignPage} />
			<Route
				path='/TheChat_App/chat'
				render={
					props =>
						isLoggedIn ? (
							<ChatPage {...props} user={userInfo} />
						) : (
							<Redirect to='/TheChat_App' />
						)
					//<ChatPage {...props} user={userInfo} />
				}
			/>
		</div>
	);
}

export default App;
